import React, { Component } from 'react';
import { Breadcrumbs, Card, CardContent, CardMedia, Container, Grid, Link, Paper, TextField, Typography } from '@material-ui/core';
import HomeIcon from '@material-ui/icons/Home';
import logo from '../../logo.svg';
import { consumerFirebase } from '../../Server';
const style = {
  cardGrid: {
    paddingTop: 8,
    paddingBottom: 8
  },
  paper: {
    backgroundColor: "#ffffff",
    
    padding: "20px",
    minHeight: 550
  },
  link: {
    display: "flex"
  },
  gridTextfield: {
    marginTop: "20px"
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column"
  },
  cardMedia: {
    paddingTop: "56.25%"
  },
  cardContent:{
    flexGrow: 1
  }
}

class ListaInmuebles extends Component {
  state = {
    inmuebles: [],
    textoBusqueda: ""
  }
  cambiarBusquedaTexto = e => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  async componentDidMount() {
    let objectQuery = this.props.firebase.db.collection('Inmuebles').orderBy('direccion');

    const snapshot = await objectQuery.get();

    const arrayInmueble = snapshot.docs.map(doc => {
      let data = doc.data();
      let id = doc.id;
      return { id, ...data };
    })

    this.setState({
      inmuebles: arrayInmueble
    })
  }

  render() {
    return (
      <Container style={style.cardGrid}>
        <Paper style={style.paper}>
          <Grid item xs={12} sm={12}>
            <Breadcrumbs aria-label="breadcrumbs">
              <Link color="inherit" style={style.link} href="/">
                <HomeIcon /> Inicio
            </Link>
              <Typography color="textPrimary">Mis Inmuebles</Typography>
            </Breadcrumbs>
          </Grid>
          <Grid item xs={12} sm={12} style={style.gridTextfield}>
            <TextField
              fullWidth
              InputLabelProps={{
                shrink: true
              }}
              name="textoBusqueda"
              variant="outlined"
              label="Ingrese el inmueble a buscar"
              onChange={this.cambiarBusquedaTexto}
              value={this.state.textoBusqueda}
            />
          </Grid>
          <Grid item xs={12} sm={12}>
            <Grid container spacing={4}>
              {this.state.inmuebles.map(card => (
                <Grid item key={card.id} xs={12} sm={6}>
                  <Card style={style.card}>
                    <CardMedia style={style.cardMedia}
                      image={
                        card.fotos
                          ? card.fotos[0]
                            ? card.fotos[0]
                            : logo
                          : logo}
                      title="Mi Inmueble" />

                    <CardContent style={style.cardContent}>
                      <Typography gutterBottom variant="h5" component="h2">
                        {card.ciudad + ", " + card.pais}
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))}

            </Grid>
          </Grid>
        </Paper>
      </Container>
    );
  }
}

export default consumerFirebase(ListaInmuebles);


















